.footer {
    background-color: var(--tertiary-color);
    width: 100%;
    padding: 120px 7.03125% 83px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @media (max-width: 768px) {
        padding-top: 60px;
    }

    .footer-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;

        @media (max-width: 768px) {
            flex-direction: column;
            justify-content: center;
        }

        .logo {
            font-family: "Poppins", sans-serif;
            font-weight: 700;
            color: var(--light-color);
            font-size: 2.25rem;
        }

        .social-icons {
            display: flex;
            justify-content: space-between;
            width: 19%;

            @media (max-width: 1200px) {
                width: 25%;
            }

            @media (max-width: 768px) {
                width: 100%;
                padding: 0 10%;
                margin-top: 20px;
            }

            .icon {
                height: 1.75rem;
                width: 1.75rem;
            }
        }
    }

    .footer-divider {
        width: 99%;
        height: 2px;
        background-color: var(--secondary-color);
        margin: 65px 0 45px;

        @media (max-width: 768px) {
            margin: 40px 0;
        }
    }

    .footer-bottom {
        font-size: 1.2rem;
        font-weight: 400;
        text-align: center;
        color: var(--light-color);

        span {
            font-family: "Poppins", sans-serif;
            font-weight: 700;
        }
    }
}
